@font-face {
  font-family: "SpoqaHanSans";
  font-weight: 50;
  src: url("./fonts/SpoqaHanSansNeo-Regular.otf") format("truetype");
}
html,body{
  font-family: 'SpoqaHanSans',AppleSDGothic !important;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
